@use "../util" as u;

html {
  height: 100%;

  font-size: 100%;

  margin: 0;
}

//Wildcard selector
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  font-family: u.$font;
  background-color: var(--background-color);
  color: var(--text-color);
}

// ul {
//   padding: 0;
//   margin: 0;
// }

svg {
  display: block;
  margin: 0 auto;
  text-align: center;
  max-height: 24px;
}
figure{
  margin: 0!important;
}
